ul {
display: flex;
flex-wrap: wrap;
justify-content: center;
list-style: none;
padding: 0;
margin: 0;

max-height: 93vh;
overflow-y: auto;
overflow-x: hidden;
}

ul::-webkit-scrollbar {
    width: 0px;
}

ul::-webkit-scrollbar-track {
    background-color: transparent;
}

ul::-webkit-scrollbar-thumb {
    background-color: transparent;
}

li {
flex: 0 0 calc(20% - 20px);
margin: 10px;
border: 1px solid #ccc;
padding: 10px;
text-align: center;
}

li:not(.listened):hover {
    transition: 0.2s ease-in-out;
    box-shadow: 0 0 10px white;
}

.listened {
    background-color: rgba(128, 128, 128, 0.5);
    cursor: default;
    display: flex;
}

.listened .info {
    position: relative;
    flex-grow: 1;
    filter: blur(2px);
}

.listened .listened-text {
    position: relative;
}

li a {
    position: relative;
    width: 100%;
    height: 100%;
    color: white;
    text-decoration: none;
}

p {
    font-size: 24px;
}

img {
    position: relative;
    width: 90%;
    user-select: none;
}

.music-footer {
    position: absolute;
    box-shadow: 0 0 10px black;
    bottom: 0px;
    height: 7vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.music-footer-button {
    margin: 7px;
    background-color: transparent;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 3vh;
    font-family: 'Roboto-Black', sans-serif;
    font-weight: 900;
}

.music-footer-button:hover {
    transition: 0.25s ease-in-out;
    text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.music-footer-button:not(:hover) {
    transition: 0.25s ease-in-out;
    text-shadow: none;
}