.auth-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80vh;
    width: 100vw;
}

.auth-container form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.auth-container button {
    margin-top: 20px;
    align-self: center;
    font-size: 1.5rem;
    font-family: 'Roboto-Black', sans-serif;
    font-weight: 900;
    color: #fff;
    background: transparent;
    border: 2px solid #fff;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.auth-container button:hover {
    background-color: #fff;
    color: #89216B;
}

@media (min-width: 300px) {
    .auth-container h2 {
        position: relative;
        font-size: 32px;
        margin: auto;
    }

    .auth-container input {
        width: 170px;
        font-size: 18px;
        margin: auto;
    }
}

@media (min-width: 800px) {
    .auth-container h2 {
        position: relative;
        font-size: 64px
    }

    .auth-container input {
        width: 350px;
        font-size: 24px;
        margin: auto;
    }
}