.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

h1 {
  font-size: 6rem;
  font-family: 'Roboto-Black', sans-serif;
  font-weight: 900;
  color: #fff;
  user-select: none;
}

h2 {
  font-size: 6rem;
  font-family: 'Roboto-Black', sans-serif;
  font-weight: 900;
  color: #fff;
}

.home_button {
  width: 400px;
  margin: 1rem;
  padding: 1rem 2rem;
  font-size: 2rem;
  font-family: 'Roboto-Black', sans-serif;
  font-weight: 900;
  color: #fff;
  background: transparent;
  border: 2px solid #fff;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  user-select: none;
}

.home_button:not(:disabled):hover {
  background-color: #fff;
  color: #89216B;
}

.home_button:disabled {
  border: 2px solid rgba(177, 177, 177, 0.5);
  border-radius: 4px;
  color: rgba(177, 177, 177, 0.5);
}

.button-container {
  display: flex;
  justify-content: center;
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: transparent;
  height: 50px;
  display: flex;
  justify-content: left;
  align-items: center;
  box-shadow: 0 0 10px #720026;
}

.navbar a {
  margin-left: 20px;
  font-family: 'Roboto-Black', sans-serif;
  font-weight: 900;
  font-size: 2vw;
  color: white;
  text-decoration: none;
  transition: all 0.3s ease;
}

.navbar a:hover {
  text-shadow: 0 0 20px #720026;
}

.members_container {
  position: relative;
  background-color: transparent;
  overflow: hidden;
}

.auth_button {
  background-color: transparent;
  border: none;
  font-size: 34px;
  color: white;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.auth_button:hover svg {
  transition: 0.3s ease-in-out;
  filter: drop-shadow(0 0 5px #fff);
}