@media (min-width: 300px) {
    .nav-container {
        height: 7vh;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0 0 10px black;
    
    }
    
    .home-button {
        margin-left: 10px;
        margin-top: -4px;
        line-height: 1;
        color: white;
        text-decoration: none;
        font-size: 3vh;
        font-family: 'Roboto-Black', sans-serif;
        font-weight: 900;
        text-shadow: none;
    }
}

@media (min-width: 600px) {
    .nav-container {
        height: 7vh;
        display: flex;
        align-items: center;
        justify-content: left;
    
    }
    
    .home-button {
        margin-left: 10px;
        margin-top: -4px;
        line-height: 1;
        color: white;
        text-decoration: none;
        font-size: 4vh;
        font-family: 'Roboto-Black', sans-serif;
        font-weight: 900;
        text-shadow: none;
    }
}

.home-button:hover {
    transition: 0.25s ease-in-out;
    text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.home-button:not(:hover) {
    transition: 0.25s ease-in-out;
    text-shadow: none;
}