:root {
  --incet-color-5: rgba(0, 255, 225, 0.5);
  --incet-color-0: rgba(25, 135, 255, 0);
}

.item-enter {
  opacity: 0;
  transform: translateY(100%);
}

.item-enter-active {
  opacity: 1;
  transform: translateY(0%);
  transition: all 1000ms ease-in;
}

.user-list {
  max-height: 86vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.user-list::-webkit-scrollbar {
  width: 0px;
}

.user-list::-webkit-scrollbar-track {
  background-color: transparent;
}

.user-list::-webkit-scrollbar-thumb {
  background-color: transparent;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  transition: all 1s ease;
}

li {
  background-color: transparent;
  margin: 5px;
  box-shadow: 0 0 10px rgb(44, 2, 2);
  border-radius: 3px;
  box-sizing: border-box;
  cursor: pointer;
  padding: 20px;
  width: 99.5vw;
  
  color: white;
  font-family: 'Roboto-Black', sans-serif;
  font-weight: 900;
  font-size: 1.5vw;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  user-select: none;
}

.current {
  position: relative;
  z-index: 1;
  animation: pulse 8s ease-in-out infinite;
}

@keyframes pulse {
  0% {
    box-shadow:
      inset 0 0 10px 2px rgba(25, 135, 255, 0),
      inset 0 0 10px 4px rgba(25, 135, 255, 0),
      inset 0 0 10px 6px rgba(25, 135, 255, 0),
      inset 0 0 10px 8px rgba(25, 135, 255, 0),
      0 0 10px rgb(44, 2, 2);
  }
  50% {
    box-shadow:
      inset 0 0 10px 2px var(--incet-color-5),
      inset 0 0 10px 4px var(--incet-color-5),
      inset 0 0 10px 6px var(--incet-color-5),
      inset 0 0 10px 8px var(--incet-color-5),
      0 0 10px rgb(44, 2, 2);
  }
  100% {
    box-shadow:
      inset 0 0 10px 2px rgba(25, 135, 255, 0),
      inset 0 0 10px 4px rgba(25, 135, 255, 0),
      inset 0 0 10px 6px rgba(25, 135, 255, 0),
      inset 0 0 10px 8px rgba(25, 135, 255, 0),
      0 0 10px rgb(44, 2, 2);
  }
}

.copy-button {
  width: 0px;
  height: 0px;
  visibility: hidden;
}

.join-footer {
  position: absolute;
  box-shadow: 0 0 10px black;
  bottom: 0px;
  height: 7vh;
  width: 100%;
  display: flex;
  align-items: center;
}

.join-footer-button {
  margin: 7px;
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 3vh;
  font-family: 'Roboto-Black', sans-serif;
  font-weight: 900;
}

.join-footer-button:hover {
  transition: 0.25s ease-in-out;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.join-footer-button:not(:hover) {
  transition: 0.25s ease-in-out;
  text-shadow: none;
}

.join-footer h3 {
  margin: auto;
  padding-right: 5vw;
  color: white;
  font-size: 3vh;
  font-family: 'Roboto-Black', sans-serif;
  font-weight: 900;
}